.list li {
  display: flex;
  justify-content: space-between;
  margin: 20px 30px 20px 20px;
  line-height: 1.5;
}
.list li div {
  overflow: hidden;
}
.list li div h3 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 15px;
  color: #333;
  font-weight: bold;
}
.list li div p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #777;
}
.list li div .icon {
  color: #999;
  font-size: 18px;
}
.list li div:last-child {
  width: 12px;
  margin-left: 10px;
}
.list li.active div h3,
.list li.active div .icon,
.list li.active div p {
  color: #108ee9;
}
