.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: #fff;
  z-index: 99;
}
.container .top {
  position: relative;
  height: 44px;
  line-height: 44px;
  overflow: hidden;
}
.container .top .back {
  position: absolute;
  left: 15px;
  color: #108ee9;
}
.container .top .switch {
  display: flex;
  height: 30px;
  width: 240px;
  margin: 6px auto 0;
  border-radius: 5px;
  color: #ffffff;
  background-color: #ddd;
  font-size: 16px;
  font-weight: bold;
  overflow: hidden;
}
.container .top .switch div {
  width: 50%;
  height: 30px;
  line-height: 30px;
  text-align: center;
}
.container .top .switch .active {
  background-color: #108ee9;
}
.container .empty {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
  text-align: center;
  font-size: 12px;
  color: #777;
}
.container .empty div {
  font-size: 88px;
  color: #ddd;
  margin-bottom: 10px;
}
.container .empty p {
  line-height: 1.5;
}
